.star_container {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 13px;
}

.star_font{
	transform: translateY(3%);
}

.star {
  width: 14px;
}

.divide{
	margin: 0 0.4rem;
	font-weight: 500;
	font-size: 10px;
	line-height: 21px;
	letter-spacing: -0.005em;
	color:#EFEFEF;
}

.star_rating {
  margin-left: 2px;
  font-weight: 600;
  font-size: 11px;
  line-height: 13px;
  color: #666666;
}
